/* Reset some default styles */
body, h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

/* Global Styles */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  color: #343a40;
}

.container {
  width: 80%;
  margin: 0 auto;
}

/* Header Section */
header {
  background-color: #007bff;
  color: #ffffff;
  padding: 20px 0;
}

header h1 a {
  color: #ffffff;
  text-decoration: none;
}

header h2 {
  color: #ffffff;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.navbar ul {
  list-style: none;
  display: flex;
}

.navbar li {
  margin-right: 20px;
}

.navbar a {
  text-decoration: none;
  color: #ffffff;
  font-weight: bold;
  font-size: 16px;
}

.social-links a {
  margin-right: 10px;
  color: #ffffff;
  font-size: 24px;
}

/* About Section */
.about {
  padding: 60px 0;
}

.about-me img {
  max-width: 100%;
  height: auto;
}

.about-me h3 {
  color: #007bff;
}

/* Projects Section */
.projects {
  padding: 60px 0;
}

.icon-box {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.icon-box i {
  font-size: 36px;
  color: #007bff;
}

.icon-box h4 {
  margin-top: 15px;
  font-size: 20px;
}

.icon-box p {
  margin-top: 10px;
}

/* Contact Section */
.contact {
  padding: 60px 0;
}

.info-box {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.info-box i {
  font-size: 36px;
  color: #007bff;
}

.info-box h3 {
  margin-top: 15px;
  font-size: 20px;
}

.info-box p {
  margin-top: 10px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .container {
    width: 90%;
  }

  header h1 {
    font-size: 24px;
  }

  header h2 {
    font-size: 14px;
  }

  .navbar ul {
    flex-direction: column;
  }

  .navbar li {
    margin: 10px 0;
  }

  .icon-box, .info-box {
    margin-bottom: 30px;
  }
}
